import AppRadio from "./app-radio";

export default {
    name: 'app-radio-group',
    components: {AppRadio},
    props: {
        type: {
            default: 'default',
        },
        value: {
            default: null,
        },
        list: {
            type: Array,
            default: [],
        },
        height: {
            type: Number,
            default: 88,
        },
        sign: {
            default: null,
        },
        color: {
            default: '#ff4544',
        }
    },
    data() {
        const list = this.list;
        for (let i in list) {
            if (list[i].label === this.value) {
                list[i].value = true;
            }
        }
        return {
            model: this.list,
        };
    },
    methods: {
        handleInput(e, index) {
            if (e === false) {
                this.model[index].value = true;
                return;
            }
            for (let i in this.model) {
                if (index != i) {
                    this.model[i].value = false;
                }
            }
            this.$emit('input', this.model[index].label, this.sign);
            this.$emit('change', this.model, this.sign);
        },
        handleClick(index) {
            for (let i in this.model) {
                if (i == index) {
                    if (!this.model[i].value) {
                        this.model[i].value = true;
                        this.$emit('input', this.model[index].label, this.sign);
                        this.$emit('change', this.model, this.sign);
                    }
                } else {
                    this.model[i].value = false;
                }
            }
        },
    },
}
